import type { Ride } from "../types/main"
import {
  Card,
  CardHeader,
  CardTitle,
  CardDescription,
  CardContent,
  CardFooter,
} from "@/components/ui/card"

export default function RideCard(ride: Ride) {
  const convertedMonth = (ride.dateTime.getMonth() + 1) % 13
  const day = ride.dateTime.getDate()
  const year = ride.dateTime.getFullYear()
  const hours = ride.dateTime.getHours()
  const minutes = String(ride.dateTime.getMinutes()).padStart(2, "0")

  return (
    <Card>
      <CardHeader>
        <CardTitle>
          Route: {ride.beginning} â†’ {ride.destination}
        </CardTitle>
        <CardDescription>
          {/* Additional info, e.g. ride ID or short note */}
          Ride ID: {ride.id}
        </CardDescription>
      </CardHeader>
      <CardContent>
        <p>
          Date: {convertedMonth}/{day}/{year}
        </p>
        <p>
          Start time: {hours}:{minutes}
        </p>
      </CardContent>
      <CardFooter>
        {/* Optional: place for buttons, tags, etc. */}
      </CardFooter>
    </Card>
  )
}
